<!-- SCSS -->
<style lang="scss">
@import 'src/scss/index.scss';
#home {
  margin: 0 .18rem;
}

#quick-nav {
  display: flex;
  flex-flow: row wrap;

  width: 100%;
  margin-top: .26rem;
  &>li {
    box-sizing: border-box;
    width: 50%;
    padding: .03rem;
    &>a {
      text-align: center;
      line-height: .44rem;

      display: block;
      border-radius: .02rem;

      background: #f6f6f6;
      -webkit-tap-highlight-color: rgba(0, 0, 0, .05);
    }
  }
}

.load-more {
  margin-top: 25px;
  margin-bottom: 50px;
  height: .4rem;
  background: url('/static/img/body/home/loading_grey.gif') 50% 50% no-repeat;
  background-size: .24rem .24rem;
}

.no-more {
  text-align: center;
  line-height: 3;
}

.first-loading {
  position: fixed;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;

  @include bgImgRem(.4, .4, '/static/img/loading_green.gif');
}
</style>

<!-- HTML -->
<template lang="pug">
#home
  ul#quick-nav
    li(v-for="v in quickNavData")
      a {{v.text}}
  #home-content(v-if="contentData.length!=0",v-for="item in contentData")
    home-item(v-for="v in item", :data="v")

  .first-loading(v-if="firstLoaing")
  .load-more(v-if="!allLoaded&&!firstLoaing")
  .no-more(v-if="allLoaded") 
</template>

<!-- JS -->
<script>
import homeItem from './home/home-item.vue'
import homeData1 from 'src/mockData/home/homeData-1.js'

export default {
  name: 'home',
  components: {
    homeItem
  },
  data() {
    return {
      msg: 'this is from home.vue',
      isLoading: false,
      allLoaded: false, // 5
      count: 1, // 
      maxCount: 10, //5
      contentData: [],
      quickNavData: [{ text: '' }, { text: '' }, { text: '' }, { text: 'app' }],
    }
  },
  computed: {
    firstLoaing() {
      return this.contentData.length === 0
    }
  },
  methods: {

  },
  created() {
    // 
    // 
    setTimeout(() => {
      this.contentData.push(homeData1.recommend_feeds)
    }, 1000)
  },
  mounted() {
    window.addEventListener('scroll', () => {
      //  
      if (this.$route.path != '/home' || this.allLoaded) return

      // 100px 
      // && 
      if (document.body.scrollHeight - window.innerHeight - document.body.scrollTop <= 100 && !this.isLoading) {
        this.isLoading = true
        console.log('')

        // ,1s
        setTimeout(() => {
          this.contentData.push(homeData1.recommend_feeds)
          console.log('')

          // 
          this.count++
          if (this.count === this.maxCount) this.allLoaded = true
          // 
          this.isLoading = false
        }, 1000)
      }
    })
  }
}
</script>



